// Переменные, миксины, функции
@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
// CSS-файлы библиотек
@import "vendor/libs";
// Основное
@import "base/fonts";
@import "base/general";
// БЭМ-блоки
@import "../blocks/modules";
/*Обнуление*/

* {
    padding: 0;
    margin: 0;
    border: 0;
}

*,
*:before,
*:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

:focus,
:active {
    outline: none;
}

a:focus,
a:active {
    outline: none;
}

nav,
footer,
header,
aside {
    display: block;
}

html,
body {
    height: 100%;
    width: 100%;
    font-size: 100%;
    line-height: 1;
    font-size: 14px;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
    font-family: inherit;
}

input::-ms-clear {
    display: none;
}

button {
    cursor: pointer;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

a,
a:visited {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

ul li {
    list-style: none;
}

img {
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
    font-weight: 400;
}

body {
    font-family: "PT Sans Caption", sans-serif;
}

.container {
    max-width: 1440px;
    margin: 0 auto;
    padding: 0px 20px;
    height: 100%;
    min-height: 100%;
}

// header
.header {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #333;
    width: 100%;
    height: 86px;
    color: #fff;
    padding: 16px 0;
    z-index: 2000;
    @media (max-width: 768px) {
        padding: 10px 0;
        height: 50px;
    }
    @media (max-width: 1024px) {
        display: flex;
        align-items: center;
    }
}

.header-icon {
    cursor: pointer;
    max-width: 281px;
    max-height: 51px;
    img {
        display: flex;
        width: 100%;
        height: auto;
        align-items: center;
    }
}

.header-information {
    cursor: pointer;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    @media (max-width: 1024px) {
        display: none;
    }
}

.header-phone {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-left: 30px;
    margin-left: 5px;
    font-weight: 400;
    font-size: calc(13px + 3 * (100vw / 2600));
    line-height: 25px;
    opacity: 0.7;
    transition: 0.6s;
    &:hover {
        opacity: 2;
    }
    a {
        color: #fff;
        strong {
            color: #bf9037;
        }
    }
    &::after {
        position: absolute;
        content: url(../img/tel.svg);
        top: 4px;
        left: 0;
    }
    @media (max-width: 1024) {
        line-height: 20;
    }
    @media (max-width: 768px) {
        font-size: calc(13x + (3 + 3 * 0.7) * ((100vw - 320px) / 2600));
    }
}

.header-locaion {
    position: relative;
    font-weight: 400;
    font-size: calc(13px + 3 * (100vw / 2600));
    line-height: 25px;
    color: #fff;
    padding-left: 30px;
    &:after {
        position: absolute;
        content: url(../img/navi.svg);
        top: 4px;
        left: 0;
    }
    opacity: 0.7;
    transition: 0.6s;
    &:hover {
        opacity: 2;
    }
    @media (max-width: 768px) {
        font-size: calc(13x + (3 + 3 * 0.7) * ((100vw - 320px) / 2600));
    }
    @media (max-width: 1024) {
        line-height: 20;
    }
}

.header-button {
    display: flex;
    align-items: center;
    @media (max-width: 1024px) {
        display: none;
    }
    button {
        border: 1.2px solid #c8ab48;
        padding: 12px 85px;
        border-radius: 20px;
        background-color: #333;
        font-size: calc(13px + 5 * (100vw / 2600));
        color: #fff;
        transition: 0.6s;
        &:hover {
            opacity: 2;
            background-color: darken(#333, 20%);
        }
        @media (max-width: 1440px) {
            padding: 12px 50px;
        }
        @media (max-width: 1280px) {
            padding: 12px 30px;
        }
    }
    @media (max-width: 768px) {
        font-size: calc(13x + (5 + 5 * 0.7) * ((100vw - 320px) / 2600));
    }
}
.burger-menu {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 15px;
    width: 50px;
    height: 30px;
    cursor: pointer;

    &__link {
        position: relative;
        display: none;
        height: 2px;
        width: 30px;
        background-color: #bf9037;

        &::after {
            content: "";
            position: absolute;
            height: 2px;
            width: 30px;
            background-color: #bf9037;
            top: -10px;
        }
        &::before {
            content: "";
            position: absolute;
            height: 2px;
            width: 30px;
            background-color: #bf9037;
            top: 10px;
        }
        @media (max-width: 1024px) {
            display: block;
        }
    }
}

.burger-nav {
    position: fixed;
    top: 0;
    left: 100%;
    width: 100%;
    min-height: 100vh;
    display: block;
    z-index: 1500;
    background-color: #333;
    margin-top: 86px;
    transition: 2s;
    @media (max-width: 768px) {
        margin-top: 50px;
    }
    a {
        display: block;
        margin-top: 30px;
    }
    &__phone {
        position: relative;
        margin-left: 60px;
        font-weight: 400;
        font-size: calc(13px + 3 * (100vw / 2600));
        line-height: 25px;
        color: #fff;
        strong {
            color: #bf9037;
        }
        &::after {
            position: absolute;
            content: url(../img/tel.svg);
            top: 4px;
            left: -30px;
        }

        @media (max-width: 1024) {
            line-height: 20;
        }
        @media (max-width: 768px) {
            font-size: calc(13x + (3 + 3 * 0.7) * ((100vw - 320px) / 2600));
        }
    }

    &__button {
        display: flex;
        align-items: center;
        margin-top: 30px;
        margin-left: 30px;

        button {
            border: 1.2px solid #c8ab48;
            padding: 12px 85px;
            border-radius: 20px;
            background-color: #333;
            font-size: calc(13px + 5 * (100vw / 2600));
            color: #fff;
            @media (max-width: 1440px) {
                padding: 12px 50px;
            }
            @media (max-width: 1280px) {
                padding: 12px 30px;
            }
        }
        @media (max-width: 768px) {
            font-size: calc(13px + (5 + 5 * 0.7) * ((100vw - 320px) / 2600));
        }
    }
}

.active {
    left: 0;
}

.row-header {
    display: flex;
    align-items: center;
    justify-content: center;
    @media (max-width: 1024px) {
        justify-content: space-between;
    }
}

// header

// intro
.intro {
    background-color: #1f090e;
    height: 100%;
    padding-top: 100px;
    &::before {
        position: absolute;
        content: "";
        height: 1px;
        right: 0;
        top: 30px;
        background-color: #bf9037;
    }
    @media (max-width: 768px) {
        padding-top: 60px;
    }
}

.intro-left {
    padding-top: 80px;
    padding-bottom: 20px;
    padding-left: 20px;
    @media (max-width: 1280px) {
        padding-top: 30px;
    }
    @media (max-width: 768px) {
        padding: 20px 0px 0px 0px;
    }
    &__title {
        margin-bottom: 50px;
        @media (max-width: 1440px) {
            margin-bottom: 30px;
        }
    }
    &__title__1 {
        h3 {
            font-size: calc(20px + 10 * (100vw / 2600));
            color: #fff;
            font-weight: 700;
            @media (max-width: 1280px) {
                font-size: 25px;
            }
            @media (max-width: 768px) {
                text-align: center;
                font-size: calc(20px + (10 + 10 * 0.7) * ((100vw - 320px) / 2600));
            }
        }
    }
    &__title__2 {
        h1 {
            font-family: "PT Sans Caption", sans-serif;
            font-size: calc(35px + 40 * (100vw / 2600));
            color: #bf9037;
            font-weight: 700;
            line-height: 100px;
            text-transform: uppercase;
            @media (max-width: 1440px) {
                font-size: 80px;
            }
            @media (max-width: 1024px) {
                line-height: 80px;
            }
            @media (max-width: 768px) {
                text-align: center;
                font-size: calc(35px + (40 + 40 * 0.7) * ((100vw - 320px) / 2600));
                line-height: 45px;
            }
        }
    }
    &__title__3 {
        h3 {
            font-size: calc(20px + 10 * (100vw / 2600));
            color: #fff;
            font-weight: 700;
            @media (max-width: 1280px) {
                font-size: 25px;
            }
            @media (max-width: 768px) {
                text-align: center;
                font-size: calc(20px + (10 + 10 * 0.7) * ((100vw - 320px) / 2600));
            }
        }
    }
}

.intro-right {
    display: flex;
    &__img {
        max-width: 605px;
        img {
            max-width: 100%;
            height: auto;
        }
    }
    @media (max-width: 1200px) {
        margin-top: 50px;
    }
    @media (max-width: 534px) {
        margin-top: 0;
    }
}

.row {
    display: flex;
}

.intro-right__info {
    padding-top: 134px;
}

.intro-right__info__position {
    color: #fff;
}

.intro-left__items {
    position: relative;
    min-height: 250px;
    @media (max-width: 1440px) {
        min-height: 225px;
    }
    @media (max-width: 768px) {
        display: none;
    }

    &::before {
        position: absolute;
        content: "";
        height: 1px;
        width: 113px;
        bottom: 0;
        left: 0;
        background-color: #bf9037;
    }
    border-left: 1px solid #bf9037;

    .intro-left__item {
        &__titles {
            margin-bottom: 30px;
        }
        &__title {
            position: relative;
            font-size: 30px;
            font-weight: 400;
            color: #bf9037;
            margin-bottom: 40px;
            padding-left: 30px;
            @media (max-width: 1440px) {
                margin-bottom: 20px;
                font-size: 25px;
            }
            &:last-child {
                margin-bottom: 93px;
                @media (max-width: 1440px) {
                    margin-bottom: 0px;
                }
            }
            &:after {
                position: absolute;
                content: "";
                left: -8px;
                top: 31%;
                height: 15px;
                width: 15px;
                border-radius: 50%;
                background-color: #bf9037;
            }
        }
        &__button {
            button {
                position: absolute;
                left: 113px;
                bottom: -43px;
                border: 1.2px solid #c8ab48;
                padding: 25px 77px;
                border-radius: 40px;
                background-color: #bf9037;
                z-index: 1000;
                font-size: calc(14px + 11 * (100vw / 2600));
                color: #333;
                transition: 0.6s;
                &:hover {
                    background-color: darken(#bf9037, 10%);
                }
                @media (max-width: 1280px) {
                    left: 60px;
                    bottom: -33px;
                    font-size: 20px;
                }

                @media (max-width: 1024px) {
                    left: 10px;
                    display: flex;
                }
                @media (max-width: 768px) {
                    font-size: calc(14px + (11 + 11 * 0.7) * ((100vw - 320px) / 2600));
                }
            }
        }
    }
}

.intro-right {
    position: relative;
    @media (max-width: 768px) {
        position: static;
        display: flex;
        justify-content: space-between;
    }
    @media (max-width: 534px) {
        flex-direction: column;
    }
    &__img {
        display: flex;
        align-items: flex-end;
        max-width: 605px;
        max-height: 762px;
        img {
            max-width: 100%;
            height: auto;
        }
    }
    &__info {
        position: absolute;
        left: 433px;
        max-width: 280px;
        right: 0;
        @media (max-width: 1440px) {
            left: 360px;
            padding-top: 60px;
        }
        @media (max-width: 1024px) {
            padding-top: 100px;
        }
        @media (max-width: 768px) {
            position: static;
            min-width: 0;
            left: 320px;
            padding-top: 20px;
        }
        &__name {
            position: relative;
            font-weight: 400;
            font-size: calc(12px + 4 * (100vw / 2600));
            line-height: 25px;
            color: #fff;
            margin-bottom: 22px;
            padding-bottom: 8px;
            border-bottom: 1px solid #bf9037;
            @media (max-width: 1400px) {
                font-size: 15px;
            }
            @media (max-width: 768px) {
                font-size: calc(12px + (4 + 4 * 0.7) * ((100vw - 320px) / 2600));
                margin-bottom: 15px;
            }
            @media (max-width: 1200px) {
                border-bottom: 1px solid #bf9037;
            }
        }
        &__position {
            font-weight: 400;
            font-size: calc(12px + 4 * (100vw / 2600));
            line-height: 25px;
            color: #bf9037;
            @media (max-width: 768px) {
                font-size: calc(12px + (4 + 4 * 0.7) * ((100vw - 320px) / 2600));
            }
            @media (max-width: 534px) {
                margin-bottom: 20px;
            }
        }
    }
}

// intro

// info
.info {
    padding-top: 83px;
    position: relative;
    @media (max-width: 768px) {
        padding-top: 30px;
    }
    &::before {
        position: absolute;
        content: "";
        height: 108px;
        top: 215px;
        right: 0;
        width: 50%;
        background-color: #7d7d7d;
        z-index: 1;
        @media (max-width: 1024px) {
            display: none;
        }
    }
    .info__title__bottom {
        margin-bottom: 80px;
    }
    &__title__top {
        font-weight: 700;
        font-size: calc(25px + 15 * (100vw / 2600));
        line-height: 50px;
        color: #333;
        margin-bottom: 50px;
        @media (max-width: 768px) {
            font-size: calc(25px + (15 + 15 * 0.7) * ((100vw - 320px) / 2600));
            margin-bottom: 30px;
            line-height: 30px;
        }
    }
    &__title__bottom {
        &-left {
            font-weight: 700;
            font-size: calc(48px + 52 * (100vw / 2600));
            line-height: 60px;
            color: #bf9037;
            letter-spacing: 0.4px;

            @media (max-width: 767px) {
                font-size: calc(48px + (52 + 52 * 0.7) * ((100vw - 320px) / 2600));
            }
            @media (max-width: 1024px) {
                width: 100%;
                margin-bottom: 45px;
                display: flex;
                justify-content: center;
            }
        }
        &-right {
            font-size: calc(15px + 10 * (100vw / 2600));
            line-height: 36px;
            color: #fff;
            z-index: 1000;
            margin-bottom: 70px;

            &-text {
                padding-left: 28px;
                @media (max-width: 768px) {
                    padding-left: 10px;
                }
            }
            @media (max-width: 1280px) {
                line-height: 28px;
            }
            @media (max-width: 768px) {
                font-size: calc(15px + (10 + 10 * 0.7) * ((100vw - 320px) / 2600));
            }
            @media (max-width: 1024px) {
                background-color: #7d7d7d;
                width: 100%;
                display: flex;
                justify-content: center;
                padding: 20px 0;
                margin-bottom: 40px;
            }
        }
    }
}

.info__wrapper__items {
    .info__wrapper__item {
        &-title {
            font-size: calc(15px + 10 * (100vw / 2600));
            line-height: 36px;
            color: #333;
            @media (max-width: 767px) {
                font-size: calc(15px + (10 + 10 * 0.7) * ((100vw - 320px) / 2600));
                text-align: center;
            }
        }
        &-img__1 {
            position: relative;
            max-width: 286px;
            img {
                max-width: 100%;
                height: auto;
            }
        }
        &-img__2 {
            position: relative;
            max-width: 286px;
            img {
                max-width: 100%;
                height: auto;
            }
        }
        &-img__3 {
            position: relative;
            max-width: 286px;
            img {
                max-width: 100%;
                height: auto;
            }
        }
        &.item-1 {
            margin-bottom: 160px;
            margin-top: 60px;
            @media (max-width: 1200px) {
                display: flex;
                justify-content: flex-end;
                margin-top: 0;
                margin-bottom: 0;
            }

            @media (max-width: 1024px) {
                margin-top: 0;
                margin-bottom: 30px;
            }
            @media (max-width: 768px) {
                justify-content: center;
            }
        }
        &.item-2 {
            margin-top: 164px;
            @media (max-width: 1200px) {
                display: flex;
                justify-content: flex-start;
                margin-top: 0;
            }
            @media (max-width: 1024px) {
                margin-top: 0;
            }
            @media (max-width: 768px) {
                justify-content: center;
            }
        }
        &.item-3 {
            margin-top: 260px;
            @media (max-width: 1200px) {
                display: flex;
                justify-content: flex-end;
                margin-top: 0;
            }

            @media (max-width: 768px) {
                justify-content: center;
            }
        }
    }
}

.info__wrapper__form {
    display: flex;
    justify-content: center;
    position: relative;

    &-title {
        font-size: calc(12px + 4 * (100vw / 2600));
        line-height: 25px;
        color: #333;
        font-weight: 400;
        padding-left: 20px;
        position: relative;
        &::after {
            position: absolute;
            content: url(../img/3_4_круг.png);
            top: -86px;
            left: -131px;
            @media (max-width: 1400px) {
                top: -80px;
                left: -128px;
            }
            @media (max-width: 1280px) {
                top: -75px;
                left: -115px;
            }
            @media (max-width: 1200px) {
                display: none;
            }
        }
        &::before {
            position: absolute;
            content: url(../img/1_2_круг.png);
            top: -55px;
            right: -123px;
            @media (max-width: 1400px) {
                top: -49px;
                right: -119px;
            }
            @media (max-width: 1280px) {
                top: -44px;
                right: -115px;
            }
            @media (max-width: 1200px) {
                display: none;
            }
        }

        span {
            font-weight: 700;
            text-transform: uppercase;
        }

        @media (max-width: 767px) {
            font-size: calc(12px + (4 + 4 * 0.7) * ((100vw - 320px) / 2600));
        }
    }
}

.wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
    max-width: 350px;
    @media (max-width: 1400px) {
        margin-left: 150px;
    }
    @media (max-width: 1200px) {
        margin: 80px 0px 0px 0px;
    }
    @media (max-width: 768px) {
        margin-top: 20px;
        margin-bottom: 40px;
    }
}

.info__wrapper__form-inputs {
    .info__wrapper__form-input {
        .title {
            font-size: calc(14px + 2 * (100vw / 2600));
            line-height: 25px;
            color: #333;
            padding-left: 20px;
            font-weight: 400;
            span {
                color: #bf9037;
            }
            @media (max-width: 768px) {
                font-size: calc(14px + (2 + 2 * 0.7) * ((100vw - 320px) / 2600));
            }
        }
        input {
            background-color: rgb(247, 247, 247);
            box-shadow: inset 0px 0px 10px 0px rgba(0, 0, 0, 0.69);
            height: 60px;
            width: 100%;
            border-radius: 30px;
            margin-bottom: 15px;
            padding-left: 30px;

        }
    }
}

.info__wrapper__form-button {
    button {
        font-family: "PT Sans Caption", sans-serif;
        padding: 20px 0px;
        border-radius: 30px;
        background-color: #bf9037;
        font-size: calc(15px + 10 * (100vw / 2600));
        font-weight: 700;
        width: 100%;
        cursor: pointer;

        @media (max-width: 768px) {
            font-size: calc(15px + (10 + 10 * 0.7) * ((100vw - 320px) / 2600));
        }
    }
    .text {
        margin-left: 50px;
        margin-top: 10px;
    }
}

.imgs-1 {
    position: absolute;
    height: 3px;
    width: 513px;
    background-color: #bf9037;
    top: 58%;
    right: -506px;
    &::after {
        position: absolute;
        content: url("../img/el.png");
        top: -13px;
        @media (max-width: 1440px) {
            display: none;
        }
    }
    &::before {
        position: absolute;
        content: url("../img/Ellipse 2.png");
