        top: -4px;
        @media (max-width: 1440px) {
            display: none;
        }
    }
    @media (max-width: 1440px) {
        display: none;
    }
}

.imgs-2 {
    position: absolute;
    height: 3px;
    width: 566px;
    background-color: #bf9037;
    top: 49%;
    right: -561px;
    &::after {
        position: absolute;
        content: url("../img/el.png");
        top: -13px;
        @media (max-width: 1440px) {
            display: none;
        }
    }
    &::before {
        position: absolute;
        content: url("../img/Ellipse 2.png");
        left: 552px;
        top: -4px;
        @media (max-width: 1440px) {
