// modal-window
.modal-window {
    visibility: hidden;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2000;
    background-color: rgba(0, 0, 0, 0.8);
    transition: 0.3s;
    opacity: 0;

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        // transform: translateY(-50%);
    }

    .window {
        display: flex;
        justify-content: center;
        flex-direction: column;
        position: relative;
        align-items: center;
        margin: auto;
        height: 500px;
        width: 500px;
        background-color: #fff;
        border-radius: 20px;
        margin: 20px;
        overflow: auto;
        

        &-container {
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;
            padding: 25px;

            &__form {
                label {
                    font-size: calc(14px + 2 * (100vw / 2600));
                    line-height: 25px;
                    color: #333;
                    font-weight: 400;
                    padding-left: 20px;

                    @media (max-width: 768px) {
                        font-size: calc(14px + (2 + 2 * 0.7) * ((100vw - 320px) / 2600));
                    }
                }

                input {
                    background-color: rgb(247, 247, 247);
                    box-shadow: inset 0px 0px 10px 0px rgba(0, 0, 0, 0.69);
                    height: 60px;
                    width: 100%;
                    border-radius: 30px;
                    margin-bottom: 15px;
                    margin-top: 20px;
                    padding-left: 30px;
                }
            }

            &__button {
                margin-top: 20px;
                border: 1.2px solid #c8ab48;
                padding: 25px 77px;
                border-radius: 40px;
                background-color: #bf9037;
                z-index: 1000;
                font-size: calc(14px + 11 * (100vw / 2600));
                color: #333;
                transition: 0.6s;

                @media (max-width: 768px) {
                    font-size: calc(14px + (11 + 11 * 0.7) * ((100vw - 320px) / 2600));
                }
            }
        }
    }

    .btn-close {
        position: absolute;
        top: 20px;
        right: 20px;
        background-color: rgb(72, 72, 72);
        border-radius: 50%;
        padding: 10px;
    }
}
.modal-window__active {
   visibility: visible;
   opacity: 1;
}



